import "waypoints/lib/noframework.waypoints.min"

import { Firestore  } from '../config/firebase';

window.Firestore= Firestore
// console.log(Firestore)

let onload = function() {
  const itemsToAnimate = [
    {
      type: "single",
      items: [...document.querySelectorAll("div.underline-heading ")]
    },
    {
      type: "group",
      items: [...document.querySelectorAll('#aboutSection > div#container > div')]
    },
    {
      type: "group",
      items: [...document.querySelectorAll('#lpnp > div#container > div')]
    },
    {
      type: "group",
      items: [...document.querySelectorAll('#STATISTICS > div#container > div')]
    },
    {
      type: "single",
      items: [...document.querySelectorAll('#speakers > div.container > div')]
    },
    {
      type: "single",
      items: [...document.querySelectorAll('#sponsors > div.sponsor-category')]
    },
    
  ];
  itemsToAnimate.forEach(items => {
    WayPointGen(items);
  });
};

const Animate = function(items) {
  if (items.length > 0) {
    items.forEach(function(item, index) {
      setTimeout(
        function() {
          item.classList.add("fadeInUp");
        },
        index * 200,
        "easeInOutExpo"
      );
    });
  }
};
const WayPointGen = function(itemData) {
  let itemsToAnimate = itemData.items;
  if (itemsToAnimate.length > 0) {
    itemsToAnimate.forEach((item, index) => {
      item.classList.add("animated");
      const a = new Waypoint({
        element: item,
        handler: direction => {
          if (direction === "down" && item.classList.contains("animated")) {
            setTimeout(Animate, 200, (() => {
              return itemData.type === "single" ? [item] : itemsToAnimate;
            })());
          }
        },
        offset: "95%"
      });
    });
  }
};


document.addEventListener("DOMContentLoaded", onload, false);
